import React from "react"
import Layout from "./../components/layout"
import { Helmet } from "react-helmet"
import Gallery from "./../components/gallery"
import { StaticQuery } from "gatsby"

import "./../styles/place-template.scss"

export default ({data}) => {
    return (
	    <StaticQuery
	    query={graphql`
		    query {
			    allFile(filter: {relativeDirectory: {eq: "portraits"}}) {
				    edges {
					    node {
						    relativePath
						    childImageSharp {
							    fluid(maxWidth: 3000) {
								    ...GatsbyImageSharpFluid
							    }
						    }
					    }
				    }
			    }
		    }
	    `}
	    render={data1 => { return (
                <div className="global portraits large-header">
            <Layout
                title={"Portraits"}
            >
                <Helmet>
                    <title>Portraits | Hana Renee</title>
                </Helmet>

                <Gallery
                    className="place-gallery"
                    images={data1.allFile.edges.map(x=>x.node.childImageSharp)}
                    maxHeight = {.5}
                    minHeight = {.3}
                />
            </Layout>
            </div>
    )}}/>)
}
